<template>
    <div class="click-event-manager">
        <el-divider>{{title}}</el-divider>
        <el-form class="click-event-manager">
            <el-form-item label="">
                <el-radio-group v-model="form.type">
                    <el-radio :label="0"></el-radio>
                    <el-radio :label="1"></el-radio>
                    <el-radio :label="2"></el-radio>
                    <el-radio :label="3"></el-radio>
                </el-radio-group>
            </el-form-item>
            <div class="nav-config" v-show="form.type==0">
                <el-alert
                        title="!"
                        type="info"
                        description=" []  []">
                </el-alert>
                <el-form-item label="">
                    <el-input type="text" v-model="form.navigationConfig.page"
                              placeholder="app,:/pages/a/b"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="form.navigationConfig.type">
                        <el-radio :label="0"></el-radio>
                        <el-radio :label="1"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="form.navigationConfig.url"
                              placeholder=",: http://xxx.com"></el-input>
                </el-form-item>
            </div>
            <div class="toast-config" v-show="form.type==1">
                <el-alert
                        title="!"
                        type="info">
                    1. <br>2. <br>3. ,1=1000
                </el-alert>
                <el-form-item label="">
                    <el-input type="text" v-model="form.toastConfig.title" placeholder=":hello akira!"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="form.toastConfig.icon">
                        <el-radio label="success"></el-radio>
                        <el-radio label="loading"></el-radio>
                        <el-radio label="none"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="form.toastConfig.position">
                        <el-radio label="top"></el-radio>
                        <el-radio label="center"></el-radio>
                        <el-radio label="bottom"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="form.toastConfig.mask"></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-input-number v-model="form.toastConfig.duration" :step="100" size="small"></el-input-number>
                </el-form-item>
            </div>
            <div class="modal-config" v-show="form.type==2">
                <el-form-item label="">
                    <el-input type="text" v-model="form.modalConfig.title" placeholder=":"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="form.modalConfig.content" placeholder=":hello akira!"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="form.modalConfig.showCancel"></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="form.modalConfig.cancelText" placeholder=":"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="form.modalConfig.confirmText" placeholder=":"></el-input>
                </el-form-item>
            </div>
            <div class="custom-config" v-show="form.type==3">
                <el-form-item label="">
                    <el-input type="text" v-model="form.custom.methodName" placeholder=":onClick"></el-input>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "click-event-manager",
        props: {
            title: {
                type: String,
                default: ''
            }
        },
        data() {
            return {
                form: {
                    type: 0,
                    navigationConfig: {
                        page: '',
                        url: '',
                        type: 0
                    },
                    toastConfig: {
                        title: 'hello akira!',
                        icon: 'none', //success , loading,none
                        mask: false,
                        duration: 1500,
                        position: 'center' //top ,center,bottom

                    },
                    modalConfig: {
                        title: '',
                        content: 'hello akira!',
                        showCancel: true,
                        cancelText: '',
                        confirmText: ''
                    },
                    custom: {
                        methodName: ''
                    }
                }
            }
        }
    }
</script>

